import React from "react";
import Header from "../../components/atoms/Header";
import Paragraph from "../../components/atoms/Paragraph";

const Kategoria = () => {
  return (
    <>
      <Header>Wybierz kategori - funkcjonalno w przygotowaniu</Header>
      <Paragraph>
        Aktualnie w bazie aplikacji jest 50 polskich filmw (zwiastunw). Z
        czasem baza bdzie rozszerzana, a docelowo gracz bdzie mg wybra
        jedn spord kilku kategorii (np. Oscary, filmy festiwalowe, komedie).
      </Paragraph>
    </>
  );
};

export default Kategoria;
