import React from "react";
import Header from "../../components/atoms/Header";
import Paragraph from "../../components/atoms/Paragraph";

const Zasady = () => {
  return (
    <div>
      <Header>O aplikacji</Header>
      <p className="paragraph">
        "Co to za film" to quiz filmowy nawizujcy form do popularnego
        teleturnieju muzycznego.
      </p>
      <Header>Jak gra?</Header>
      <Paragraph>
        Na stronie gwnej zobaczysz prob o podanie liczby sekund oraz cztery
        tytuy. Po klikniciu "Graj" wywietli si losowy fragment jednego z
        czterech filmw (a konkretniej zwiastunw; np. jeli wpiszesz "10",
        zobaczysz dziesiciosekundowe wideo).
      </Paragraph>
      <Paragraph>
        Po obejrzeniu zaznacz swoj odpowied. Po klikniciu "nastpne"
        przejdziesz do kolejnego pytania. W zakadce "statystyki" znajdziesz
        liczb poprawnych i bdnych odpowiedzi oraz redni czas na waciw
        odpowied. Jeeli chcesz rozpocz rozgrywk od nowa - odwie stron.
      </Paragraph>
    </div>
  );
};

export default Zasady;
